import React from 'react';
import './ArticleClassification.css'
import {ListGroupItem,ListGroup} from 'react-bootstrap'
const ArticleClassification = () =>{
    return(
        <div className='articleClassification'>
            <ListGroup>
                <ListGroupItem className='option'>
                    <a className='optionLink'>数据结构与算法(0)</a>
                </ListGroupItem>
                <ListGroupItem className='option'>
                    <a className='optionLink'>JavaScript(0)</a>
                </ListGroupItem>
                <ListGroupItem className='option'>
                    <a className='optionLink'>前端开发(0)</a>
                </ListGroupItem>
                <ListGroupItem className='option'>
                    <a className='optionLink'>随行笔记(0)</a>
                </ListGroupItem>
                <ListGroupItem className='option'>
                    <a className='optionLink'>其他(0)</a>
                </ListGroupItem>
            </ListGroup>
        </div>
    );
}
export default ArticleClassification;