import React from 'react';
import './Header.css'
import {Nav, Navbar, NavItem} from "react-bootstrap";

const Header = () =>{
    return(
        <div>
            <Navbar className='head'>
                <Navbar.Header>
                    <Navbar.Brand>
                        <a href="#" className='logo'><p>张旭的Blog</p></a>
                    </Navbar.Brand>
                    <Navbar.Toggle />
                </Navbar.Header>
                <Navbar.Collapse>
                    <Nav pullRight className='navigation'>
                        <NavItem eventKey={1} href="#" className='a'>
                            首页
                        </NavItem>
                        <NavItem eventKey={2} href="#" className='a'>
                            留言区
                        </NavItem>
                        <NavItem eventKey={2} href="#" className='a'>
                            常见问题
                        </NavItem>
                        <NavItem eventKey={2} href="#" className='a'>
                            联系我
                        </NavItem>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        </div>
    );
}
export default Header;