import React from 'react';
import './PersonalData.css'
import {Image} from'react-bootstrap'
import avatar from './avatar.jpg'
import qq from './icon.png'
import weixin from './weixin.png'
import weibo from './weibo.png'
import github from './GitHub.png'
const PersonalData = () =>{
    return(
        <div className='PersonalData'>
            <div className='top'>
                <div className='avatar'>
                    <Image src ={avatar} circle className='image'/>
                    <div className='name'>张啊张</div>
                </div>
            </div>
            <div className='center'>
                <div className='container'>
                    <div>
                        <span className='icons'><Image src={qq} className='icon'/></span>
                        <span className='icons'><Image src={weixin} className='icon'/></span>
                        <span className='icons'><Image src={weibo} className='icon'/></span>
                        <span className='icons'><Image src={github} className='icon'/></span>
                    </div>
                </div>
            </div>
            <div className='bottom'>
                <div className='synopsis'>
                    洛阳师范学院在读学生
                </div>
                <div className='synopsis'>
                    前端开发学习博客
                </div>
                <div className='statistics'>
                    <span className='release'>
                        <div className='indivTop'>
                            发布文章
                        </div>
                        <div className='indivBottom'>
                            0
                        </div>
                    </span>
                    <span className='comment'>
                        <div className='indivTop'>
                            留言数
                        </div>
                        <div className='indivBottom'>
                            0
                        </div>
                    </span>
                    <span className='browse'>
                        <div className='indivTop'>
                            浏览人数
                        </div>
                        <div className='indivBottom'>
                            14
                        </div>
                    </span>
                </div>
            </div>
        </div>
    );
}
export default PersonalData;